/**
 * 检查是否登录
 * return boolean
 */
const isLogin = () => {
    if (uni.getStorageSync('token')) {
        return true
    }

    return false
}

/**
 * 检查登录 并跳转
 * return reLaunch
 */
const checkAuth = () => {
    if (! isLogin()) {
        uni.reLaunch({ url: '/pages/my/login' })
    }
}

const helper = {
    isLogin: isLogin,
    checkAuth: checkAuth,
}

export default helper